// ****************************
//
//  FOOTER
//
//  Mobile First (less than 768)
//
// ****************************

.site-footer{
	background-color: $c-dark-red;
	padding:30px 0;

	a{
		color:$c-white;
		text-decoration: underline;

		&:hover{
			color:$c-white;
			text-decoration: none;
		}
	}
}


.site-footer__social-icons{
	@include default-floating;
	text-align:center;
	margin-top:20px;

	li{
		display:inline-block;
	}

	li a{
		color:$c-red;
		text-decoration:none;
		display:inline-block;
        font-size:55px;
        line-height:1;
        margin-right:5px;
        transition: 0.3s;

		&:hover{
			color:$c-white;
			text-decoration:none;
		}
	}
}


.site-footer p{
	color:$c-white;
	text-align:center;
	margin-bottom:10px;
	font-size:14px;
	margin:0;
	padding:0;

	a:hover{
		color:$c-white;
	}
}



